.btn{padding:.25em .5em;font-family:"Oswald";cursor:pointer;display:flex;justify-content:center;align-items:center;gap:5px;box-sizing:border-box;width:fit-content}.btn.btn-default{border:solid thin #1de9b6;color:#0a634d}.btn.btn-default:hover:not(.btn-disabled){background:#1de9b6}.btn.btn-default.btn-disabled{border:solid thin #adadad;color:#adadad}.btn.btn-primary{border:solid thin #6200ee;background-color:#6200ee;color:#eee}.btn.btn-primary:hover:not(.btn-disabled){background:#230055;color:#b988ff}.btn.btn-primary.btn-disabled{background:#b988ff;border:solid thin #b988ff;color:#eee}.btn.btn-text{border:none;color:#6200ee;border:solid thin rgba(0,0,0,0)}.btn.btn-text:hover:not(.btn-disabled){border:solid thin #6200ee}.btn.btn-circle{border-radius:100%;width:2.5rem;height:2.5rem}.btn.btn-disabled{cursor:not-allowed}