﻿.dropdown{display:flex;align-items:center}.dropdown .dropdown-label{margin-right:1rem}.dropdown select{display:none}.dropdown .dropdown-select{background-color:#fff;cursor:pointer;user-select:none;color:#000;padding:.5rem 1rem;border-style:none;border-radius:.2rem;filter:drop-shadow(0.2rem 0.2rem 0.5rem rgba(0, 0, 0, 0.2))}.dropdown .dropdown-select ul{list-style:none;padding:0;margin:0;position:absolute;left:0;margin-top:.5em}.dropdown .dropdown-select ul li{visibility:hidden;cursor:pointer;margin:.5rem 0;border-radius:.2rem;padding:.5rem 1rem;background-color:#fff;transition:all 200ms cubic-bezier(0.47, 1.64, 0.41, 0.8)}.dropdown .dropdown-select ul li:hover{transform:translateX(0.5em)}.dropdown .dropdown-select .button{position:absolute;color:#fff;background:#6200ee;height:100%;top:0;width:1.5rem;border-radius:0 .2rem .2rem 0;right:0;display:flex;align-items:center;justify-content:center}.dropdown .dropdown-select .button::before{content:"▼";font-size:.5em}.dropdown .dropdown-select span{margin-right:1.5rem}.dropdown .dropdown-select:focus{z-index:1}.dropdown .dropdown-select:focus ul li{visibility:visible}.dropdown .dropdown-select:focus .button::before{content:"▲";font-size:.5em}