.loader{width:100vw;position:absolute;z-index:300;display:flex;justify-content:center;align-items:center;top:0;bottom:0;opacity:0;transition:opacity 1s;cursor:wait}.loader.visible{opacity:1;display:flex}.loader.mask{background-color:rgba(238,238,238,.75)}.loader .loader-default{height:30px;width:300px;background-color:#6200ee;color:#fff;line-height:30px;padding-left:5px}