.alert{position:absolute;box-shadow:0 15px 20px rgba(0,0,0,.1);z-index:300;bottom:0;transform:translateY(100px);right:0;background:#fff;visibility:hidden;opacity:0;margin:1.5rem;box-sizing:border-box;transition:all cubic-bezier(0.47, 1.64, 0.41, 0.8) 1s}.alert.visible{transition:all cubic-bezier(0.47, 1.64, 0.41, 0.8) 1s 1s;transform:translateX(0);bottom:0;visibility:visible;opacity:1}.alert .alert-wrapper{min-height:3rem;min-height:4rem;box-sizing:border-box;min-width:20rem}.alert .alert-wrapper .alert-icon{border-left:solid}.alert .alert-wrapper .alert-content .alert-buttons{gap:1rem;flex-wrap:wrap}.alert.alert-warning .alert-icon,.alert.alert-debug .alert-icon{color:#ffb700;border-color:#ffb700}.alert.alert-error .alert-icon{color:#e85600;border-color:#e85600}.alert.alert-prompt .alert-icon{color:#6200ee;border-color:#6200ee}